import Vue from "vue";
import VueRouter from "vue-router";
import store from '../store/index';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        component: () => import('@/layouts/EmptyLayout.vue'),
        children: [
          {path: '', name: 'login', component: () => import('../views/Login.vue')},
        ]
    },
    {
        path: '/',
        component: () => import('@/layouts/MainLayout.vue'),
        children: [
          {path: 'home', name: 'home', component: () => import('../views/Home/Index.vue'),
           meta: {title: 'Emiran.uz - Umumiy', authRequired: true}},
          {path: 'profile', name: 'profile', component: () => import('../views/Profile/Index.vue'),
           meta: {title: 'Emiran.uz - Profil', authRequired: true}},
          {path: 'roles', name: 'roles', component: () => import('../views/Profile/Roles/Index.vue'),
           meta: {title: 'Emiran.uz - Ruxsatlar', authRequired: true}},
          {path: 'users', name: 'users', component: () => import('../views/Profile/Users/Index.vue'),
           meta: {title: 'Emiran.uz - Foydalanuvchilar', authRequired: true}},


          {path: 'finance', name: 'finance', component: () => import('../views/Finance/Index.vue'),
           meta: {title: 'Emiran.uz - Moliya', authRequired: true}},
          {path: 'codebase', name: 'codebase', component: () => import('../views/CodeBase.vue'),
           meta: {title: 'Emiran.uz - Kodlar-bazasi', authRequired: true}},

          {path: 'warehouse/ip', name: 'warehouse-ip', component: () => import('../views/Warehouse/Thread/Index.vue'),
          meta: {title: 'Emiran.uz - Omborxona-ip', authRequired: true}},

          {path: 'warehouse/mato', name: 'warehouse-mato', component: () => import('../views/Warehouse/Material/Index.vue'),
          meta: {title: 'Emiran.uz - Omborxona-mato', authRequired: true}},

          {path: 'warehouse/yoqa', name: 'warehouse-yoqa', component: () => import('../views/Warehouse/Collar/Index.vue'),
          meta: {title: 'Emiran.uz - Omborxona-yoqa', authRequired: true}},


          {path: 'outsource/mato-toquv/:id', name: 'outsource-mato-toquv-id',
          component: () => import('../views/Outsource/MaterialWeaving/Index.vue'), meta: {title: 'Emiran.uz - Outsource-mato-toquv', authRequired: true}},


          {path: 'outsource/yoqa-toquv/:id', name: 'outsource-yoqa-toquv-id',
          component: () => import('../views/Outsource/CollarWeaving/Index.vue'), meta: {title: 'Emiran.uz - Outsource-yoqa-toquv', authRequired: true}},


          {path: 'outsource/boyoqxona/:id', name: 'outsource-boyoqxona-id', component: () => import('../views/Outsource/Painting/Index.vue'),
          meta: {title: 'Emiran.uz - Outsource-bo`yoqxona', authRequired: true}},


          {path: 'supply/tayyor-xomashyo/mato', name: 'supply-tayyor-xomashyo-mato',
          component: () => import('../views/Supply/RawReady/Material/Index.vue'), meta: {title: 'Emiran.uz - Ta`minot-mato', authRequired: true}},


          {path: 'supply/tayyor-xomashyo/yoqa', name: 'supply-tayyor-xomashyo-yoqa',
          component: () => import('../views/Supply/RawReady/Collar/Index.vue'), meta: {title: 'Emiran.uz - Ta`minot-yoqa', authRequired: true}},


          {path: 'supply/aksessuar', name: 'supply-aksessuar', component: () => import('../views/Supply/Accessory/Index.vue'),
          meta: {title: 'Emiran.uz - Ta`minot-aksessuar', authRequired: true}},


          {path: 'supply/bichuv', name: 'supply-bichuv', component: () => import('../views/Supply/Tailoring/Index.vue'),
           meta: {title: 'Emiran.uz - Ta`minot-bichuv', authRequired: true}},

          {path: 'factory/:id', name: 'factory-id', component: () => import('../views/Factory/Index.vue'),
           meta: {title: 'Emiran.uz - Fabrika', authRequired: true}},

          {path: 'ready-products/:id', name: 'ready-products-id', component: () => import('../views/Ready-Products/Index.vue'),
          meta: {title: 'Emiran.uz - Tayyor-mahsulotlar', authRequired: true}},

        ]
    }
];

const router = new VueRouter({
    routes,
    mode:'history'
});




router.beforeEach(async (to, from, next) => {
  let user = await store.state.user;
  if (to.matched.some(route => route.meta.authRequired)) {
    if(user && user.token){
      next()
    } else{
      if(sessionStorage.getItem('user')){
        const userData = JSON.parse(sessionStorage.getItem('user'));
        const permissionsData = JSON.parse(sessionStorage.getItem('permissions'));
        await store.dispatch('main/stateChanged', {user: userData, permissions: permissionsData});
        next()
      }else{
        next('/')
      }

    }
  }
  else{
    next()
  }
})



const DEFAULT_TITLE = 'Emiran.uz';
router.afterEach((to, from) => {
    // Use next tick to handle router history correctly
    // see: https://github.com/vuejs/vue-router/issues/914#issuecomment-384477609
    Vue.nextTick(() => {
        document.title = to.meta.title || DEFAULT_TITLE;
    });
});


export default router;
